import { Injectable } from '@angular/core';
import { DevicePlateform } from '@core/enums/device-plateform.enum';

@Injectable({
	providedIn: 'root',
})
export class DevicePlateformService {
	getPlatform(): string {
		const userAgent = navigator.userAgent || navigator.vendor || (window as any).opera;

		// Check for iOS
		if (/iPad|iPhone|iPod/.test(userAgent) && !(window as any).MSStream) {
			return DevicePlateform.IOS;
		}

		// Check for macOS specifically
		if (/Macintosh|Mac OS X/.test(userAgent)) {
			return 'MacOS';
		}

		// Check for Android
		if (/android/i.test(userAgent)) {
			// Additional check for Huawei
			if (/huawei/i.test(userAgent) || /honor/i.test(userAgent)) {
				return DevicePlateform.HUAWEI;
			}

			return DevicePlateform.ANDROID;
		}

		// Check for Desktop (defaulting if not mobile)
		if (!/mobile/i.test(userAgent)) {
			if (/win/i.test(userAgent)) {
				return DevicePlateform.WINDOWS_DESKTOP;
			} else if (/linux/i.test(userAgent)) {
				return DevicePlateform.LINUX;
			}

			return DevicePlateform.DESKTOP; // Generic desktop
		}

		// Default return if none match
		return DevicePlateform.UNKNOWN;
	}
}
